.portrait-wrapper {
  display: contents;
  align-items: start;
}

.portrait {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  margin: 20px;
  width: 155px;
  max-width: calc(100% - 20px - 20px);
  text-decoration: none;
}

.portrait-small {
  position: relative;
  display: inline-flex;
  // justify-content: center;
  // align-items: center;
  flex-direction: row;
  gap: 20px;
  margin: 20px;
  width: 400px;
  max-width: calc(50% - 20px - 20px);
  text-decoration: none;
  text-align: left;
}

.portrait[data-style="small"] {
  width: 100px;
}

.portrait[data-style="tiny"] {
  flex-direction: row;
  gap: 15px;
  width: unset;
  text-align: left;
}

.portrait-image {
  width: 100%;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  object-fit: cover;
  box-shadow: var(--shadow);
}

.portrait-small-image {
  width: 20%;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  object-fit: cover;
  box-shadow: var(--shadow);
}

.portrait[data-style="tiny"] .portrait-image {
  width: 50px;
}

.portrait[data-style="tiny"] .portrait-role {
  display: none;
}

.portrait-text {
  display: flex;
  flex-direction: column;
  line-height: calc(var(--spacing) - 0.4);
}

.portrait-label {
  display: flex;
  gap: 10px;
}

.portrait-name {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  color: var(--text);
}

.portrait-term{
  color: var(--gray);
  font-weight: var(--thin);
  font-size: var(--small);

}

.portrait-role .icon {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  background: var(--background);
  box-shadow: var(--shadow);
  transform: translate(14%, 14%);
}

.portrait[data-style="small"] .portrait-role .icon {
  left: -2px;
  top: -2px;
}

/* Mobile Fixes (Screens smaller than 768px) */
@media (max-width: 768px) {
  
  .portrait-small {
    max-width: calc(100% - 40px); 
    width: 100%;
    margin: 10px 20px;
    display: flex;
  }

  .portrait-small-image {
    width: 60px; 
    height: 60px;
    min-width: 60px;
    flex-shrink: 0;
    aspect-ratio: 1/ 1;
    border-radius: 50%;
    object-fit: cover;

  }
  
  .portrait-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}